<template>
  <v-row justify="center" class="mt-12 mb-12">
    <v-col
      cols="12"
      sm="10"
      md="8"
      lg="6"
    >
      <v-card id="signupForm" class="rounded-lg" elevation="0">
        <v-form ref="form" >
        <v-card-text>
            <div class="inputBoxWrap">
              <span class="formTitle"></span>
              <v-text-field
                ref="name"
                v-model="name"
                :rules="[() => !!name || '  .']"
                :error-messages="errorMessages"
                placeholder=""
                required
              ></v-text-field>
            </div>


          <div class="inputBoxWrap">
            <span class="formTitle"></span>
            <v-text-field
                ref="email"
                v-model="email"
                :rules="[() => !!email || '  .', rules.emailCheck]"
                :error-messages="errorMessages"
                placeholder="abc@abc.com"
                required
            ></v-text-field>
            <v-btn @click="duplicateCheck"></v-btn>
          </div>


          <div class="inputBoxWrap">
            <span class="formTitle"></span>
            <v-text-field
                v-model="password"
                name="password"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[() => !!password || '  .',rules.required, rules.min]"
                :type="show1 ? 'text' : 'password'"
                hint=" , ,    8 ."
                counter
                @click:append="show1 = !show1"
            ></v-text-field>
          </div>


          <div class="inputBoxWrap">
            <span class="formTitle"> </span>
            <v-text-field
                v-model="passwordCheck"
                name="passwordCheck"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, passwordMatch]"
                :type="show1 ? 'text' : 'password'"
                counter
                @click:append="show1 = !show1"
            ></v-text-field>
          </div>

          <div class="inputBoxWrap">
            <span class="formTitle"></span>
            <v-text-field
                ref="zip"
                v-model="zip"
                :rules="[() => !!zip || '  .']"
                required
                placeholder="12345"
                @click="getPostcode"
            ></v-text-field>
            <v-btn @click="getPostcode"> </v-btn>
          </div>

            <div class="inputBoxWrap">
              <span class="formTitle"></span>
          <v-text-field
            ref="address1"
            v-model="address1"
            :rules="[() => !!address1 || ' // .', addressCheck]"
            required
          ></v-text-field>
            </div>

            <div class="inputBoxWrap">
              <span class="formTitle"></span>
          <v-text-field
            ref="address2"
            v-model="address2"
            :rules="[
              () => !!address2 || '     .',
              () => !!address2 && address2.length <= 5 || '    .',
              addressCheck
            ]"
            placeholder="OO 203"
            counter="25"
            required
          ></v-text-field>
          
            </div>


        </v-card-text>
        </v-form>

        <v-card-actions  class="mt-12">
          <v-btn text outlined color="error">
            
          </v-btn>
          <v-spacer></v-spacer>
          <v-slide-x-reverse-transition>
            <v-tooltip
              v-if="formHasErrors"
              left
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  class="my-0"
                  v-bind="attrs"
                  @click="resetForm"
                  v-on="on"
                >
                  <v-icon>mdi-refresh</v-icon>
                </v-btn>
              </template>
              <span>Refresh form</span>
            </v-tooltip>
          </v-slide-x-reverse-transition>
          <v-btn
          outlined
            color="success"
            text
          type="button"
            @click="submit"
          >
             
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
  export default {
    data: () => ({
      errorMessages: '',
      name: null,
      email: null,
      password: '',
      passwordCheck: '',
      address2: null,
      address1: null,
      zip: null,
      duplicheck: false,
      formHasErrors: false,
      rules: {
        required: value => !!value || ' .',
        min: v => v.length >= 8 || 'Min 8 characters',
        emailCheck: v => !v || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || '   .',
      },
    }),

    computed: {
      passwordMatch() {
        return () => (this.password === this.passwordCheck) || '  .'
      },
      form () {
        return {
          name: this.name,
          email: this.email,
          password: this.password,
          address1: this.address1,
          address2: this.address2,
          zip: this.zip,
        }
      },
    },

    watch: {
      name () {
        this.errorMessages = ''
      }
    },

    methods: {
      duplicateCheck(){
        let that=this;
        this.duplicheck=true;

        this.$axios.post('api/user/duplicateCheck', {
          email: that.email,
        })
            .then(res =>{
              alert(res.data);
            })
            .catch(err=>{
              console.log(err);
            })
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      submit () {
        const validate = this.$refs.form.validate();
        let that=this;

        if(that.duplicheck==false){
          alert("  .");
        }

        if (validate) {
          this.$axios.post('api/user/signup', {
            name: that.name,
            password: that.password,
            zipcode: that.zip,
            email: that.email,
            address1:that.address1,
            address2:that.address2,
          })
          .then(() =>{
            alert(" ");
            this.$router.push('/login');
          })
          .catch(err=>{
            console.log(err);
          })
        }

      },
      getPostcode(){
        new window.daum.Postcode({
          oncomplete: (data) => {
            let roadAddr = data.roadAddress; //   
            let extraRoadAddr = ''; //   

            //    . ( )
            if(data.bname !== '' && /[||]$/g.test(data.bname)){
              extraRoadAddr += data.bname;
            }
            //  ,   .
            if(data.buildingName !== '' && data.apartment === 'Y'){
              extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
            }
            //    ,     .
            if(extraRoadAddr !== ''){
              extraRoadAddr = ' (' + extraRoadAddr + ')';
            }
            this.zip = data.zonecode;
            this. address1 = roadAddr+extraRoadAddr;
          }
        }).open();
      }
    },
  }
</script>
<style scoped>
formTitle {
        display: block;
        margin: 20px 0;
    }

    .inputBoxWrap {
        margin: 50px 0;
    }
</style>