<template>
  <div class="login">
    <div id="loginWrap">

        <h2>   . </h2>
        <v-form action=""  ref="form">
            <div class="inputBoxWrap">
                <span class="formTitle"></span>
                <v-text-field class="inputBox" v-model="id" label=" ." hide-details="auto" :rules="[rules2.required]" ref="id"></v-text-field>
            </div>

            <div class="inputBoxWrap">
                <span class="formTitle"></span>
                <v-text-field 
                v-model="pw" 
                ref="pw"
                class="inputBox input-group--focused" 
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required]"
                :type="show ? 'text' : 'password'"
                name="input-10-2"
                label=" ."
                @click:append="show = !show"></v-text-field>
            </div>

            <v-btn block outlined color="primary" 
            @click="checkForm">
                
            </v-btn>
<v-list-item class="mt-10" >  </v-list-item>
            <router-link to="/signup"><v-btn block outlined  color="error" >
                
            </v-btn>
            </router-link>
        </v-form>
    </div>
  </div>
</template>

<script>
  export default {
      
    data () {
      return {
        show: false,
        id: null,
        pw: null,
        formHasErrors: false,
        errorMessages: '',
        rules: {
          required: value => !!value || '  .',
          // min: value => value.length >= 8 || ' 8  .',
        },
        rules2: {
        required: value => !!value || '  .',
        // min: value => (value && value.length >= 3) || '    .',
      },
      }
    },
     computed: {
      form () {
        return {
          id: this.id,
          pw: this.pw,
        }
      },
    },

       watch: {
      name () {
        this.errorMessages = ''
      },
    },
    methods: {
      checkForm() {
        const validate = this.$refs.form.validate();
        let that = this;
        if (validate) {
          this.$axios.post('api/login', {
            password: that.pw,
            email: that.id,
          })
        .then(res => {
          let token=res.data.token;
          localStorage.setItem("access_token", token);
          const data={
            id: that.id,
          }
          this.$store.dispatch("LOGIN", data);
          // alert(" ");
          this.$router.push('/');
        })
        .catch(err => {
          console.log(err);
          alert("  . : (");
        })
        }
      }
    }
  }
</script>


<style scoped>
    .login {
        max-width: 450px;
        margin: 50px auto;
    }
    #loginWrap {
        overflow: hidden;
        background: #fafafa;
        border-radius: 15px;
        height: auto;
        padding: 25px 25px 70px;
    }

    #loginWrap h2 {
        margin-top: 50px;
        text-align: center;
    }

    .formTitle {
        display: block;
        margin: 20px 0;
    }

    .inputBoxWrap {
        margin: 50px 0;
    }
</style>